/**
 * @module EaselJS
 */


(function () {
    "use strict";


// constructor:
    /**
     * Global utility for working with multi-touch enabled devices in EaselJS. Currently supports W3C Touch API (iOS and
     * modern Android browser) and the Pointer API (IE), including ms-prefixed events in IE10, and unprefixed in IE11.
     *
     * Ensure that you {{#crossLink "Touch/disable"}}{{/crossLink}} touch when cleaning up your application. You do not have
     * to check if touch is supported to enable it, as it will fail gracefully if it is not supported.
     *
     * <h4>Example</h4>
     *
     *      var stage = new createjs.Stage("canvasId");
     *      createjs.Touch.enable(stage);
     *
     * <strong>Note:</strong> It is important to disable Touch on a stage that you are no longer using:
     *
     *      createjs.Touch.disable(stage);
     *
     * @class Touch
     * @static
     **/
    function Touch() {
        throw "Touch cannot be instantiated";
    }


// public static methods:
    /**
     * Returns `true` if touch is supported in the current browser.
     * @method isSupported
     * @return {Boolean} Indicates whether touch is supported in the current browser.
     * @static
     **/
    Touch.isSupported = function () {
        return true;
    };

    /**
     * Enables touch interaction for the specified EaselJS {{#crossLink "Stage"}}{{/crossLink}}. Currently supports iOS
     * (and compatible browsers, such as modern Android browsers), and IE10/11. Supports both single touch and
     * multi-touch modes. Extends the EaselJS {{#crossLink "MouseEvent"}}{{/crossLink}} model, but without support for
     * double click or over/out events. See the MouseEvent {{#crossLink "MouseEvent/pointerId:property"}}{{/crossLink}}
     * for more information.
     * @method enable
     * @param {Stage} stage The {{#crossLink "Stage"}}{{/crossLink}} to enable touch on.
     * @param {Boolean} [singleTouch=false] If `true`, only a single touch will be active at a time.
     * @param {Boolean} [allowDefault=false] If `true`, then default gesture actions (ex. scrolling, zooming) will be
     * allowed when the user is interacting with the target canvas.
     * @return {Boolean} Returns `true` if touch was successfully enabled on the target stage.
     * @static
     **/
    Touch.enable = function (stage) {

        if (!stage || !stage.canvas || !Touch.isSupported()) return false;
        if (stage.__touch) return true;

        // inject required properties on stage:
        stage.__touch = {pointers: {}, multitouch: true, preventDefault: true, count: 0};

        // note that in the future we may need to disable the standard mouse event model before adding
        // these to prevent duplicate calls. It doesn't seem to be an issue with iOS devices though.
        Touch._IOS_enable(stage);
        return true;
    };

    /**
     * Removes all listeners that were set up when calling `Touch.enable()` on a stage.
     * @method disable
     * @param {Stage} stage The {{#crossLink "Stage"}}{{/crossLink}} to disable touch on.
     * @static
     **/
    Touch.disable = function (stage) {
        if (!stage) return;
        Touch._IOS_disable(stage);
        stage.__touch = undefined;
    };

    // Private static methods:
    /**
     * @method _IOS_enable
     * @protected
     * @param {Stage} stage
     * @static
     **/
    Touch._IOS_enable = function (stage) {
        var canvas = stage.canvas;
        var f = stage.__touch.f = function (e) {
            Touch._IOS_handleEvent(stage, e);
        };
        canvas.addEventListener("touchstart", f, false);
        canvas.addEventListener("touchmove", f, false);
        canvas.addEventListener("touchend", f, false);
        canvas.addEventListener("touchcancel", f, false);

    };

    /**
     * @method _IOS_disable
     * @protected
     * @param {Stage} stage
     * @static
     **/
    Touch._IOS_disable = function (stage) {
        var canvas = stage.canvas;
        if (!canvas) {
            return;
        }
        var f = stage.__touch.f;
        canvas.removeEventListener("touchstart", f, false);
        canvas.removeEventListener("touchmove", f, false);
        canvas.removeEventListener("touchend", f, false);
    };

    /**
     * @method _IOS_handleEvent
     * @param {Stage} stage
     * @param {Object} e The event to handle
     * @protected
     * @static
     **/
    Touch._IOS_handleEvent = function (stage, e) {

        if (!stage) return;

        // console.log('Touch._IOS_handleEvent3',  e);


        if (stage.__touch.preventDefault) e.preventDefault && e.preventDefault();

        // console.log('Touch._IOS_handleEvent4', e, e.changedTouches.length);

        var touches = e.changedTouches;
        var type = e.type;
        for (var i = 0, l = touches.length; i < l; i++) {
            var touch = touches[i];
            var id = touch.identifier;
            // console.log('Touch eventt 5',type,touch.identifier);
            if (type === "touchstart") {
                // console.log('Touch touchstart 6',id);
                this._handleStart(stage, id, e, touch.pageX, touch.pageY);
            } else if (type === "touchmove") {
                // console.log('Touch touchmove 6',id);
                this._handleMove(stage, id, e, touch.pageX, touch.pageY);
            } else if (type === "touchend" || type === "touchcancel") {
                // console.log('Touch touchend 6',id);
                this._handleEnd(stage, id, e);
            }

            if (type === "ontouchstart") {
                // console.log(stage, id, e, touch.pageX, touch.pageY, 666)
                this._handleStart(stage, id, e, touch.pageX, touch.pageY);
            } else if (type === "ontouchmove") {
                this._handleMove(stage, id, e, touch.pageX, touch.pageY);
            } else if (type === "ontouchend" || type === "ontouchcancel") {
                this._handleEnd(stage, id, e);
            }
        }
    };


    /**
     * @method _handleStart
     * @param {Stage} stage
     * @param {String|Number} id
     * @param {Object} e
     * @param {Number} x
     * @param {Number} y
     * @protected
     **/
    Touch._handleStart = function (stage, id, e, x, y) {


        var props = stage.__touch;
        // console.log('Touch._handleStart',props);
        if (!props.multitouch && props.count) {
            return;
        }
        var ids = props.pointers;
        if (ids[id]) {
            return;
        }
        ids[id] = true;
        props.count++;
        stage._handlePointerDown(id, e, x, y);
    };

    /**
     * @method _handleMove
     * @param {Stage} stage
     * @param {String|Number} id
     * @param {Object} e
     * @param {Number} x
     * @param {Number} y
     * @protected
     **/
    Touch._handleMove = function (stage, id, e, x, y) {
        if (!stage.__touch.pointers[id]) return;
        stage._handlePointerMove(id, e, x, y);
    };

    /**
     * @method _handleEnd
     * @param {Stage} stage
     * @param {String|Number} id
     * @param {Object} e
     * @protected
     **/
    Touch._handleEnd = function (stage, id, e) {
        // TODO: cancel should be handled differently for proper UI (ex. an up would trigger a click, a cancel would more closely resemble an out).
        var props = stage.__touch;
        var ids = props.pointers;
        if (!ids[id]) {
            return;
        }
        props.count--;
        stage._handlePointerUp(id, e, true);
        delete(ids[id]);
    };


    createjs.Touch = Touch;
}());
